.Section {
  background: -webkit-gradient(linear, left top, right top, from(#3F2B96), to(#A8C0FF));
  background: -webkit-linear-gradient(left, #3F2B96 0%, #A8C0FF 100%);
  background: -o-linear-gradient(left, #3F2B96 0%, #A8C0FF 100%);
  background: linear-gradient(90deg, #3F2B96 0%, #A8C0FF 100%);
  color: white;
  padding: 2em 0 1em 0;
}

.Container h1 {
  padding: 1em 0 1.5em 0;
  font-family: 'Circular Bold';
  font-size: 32px;
  text-align: center;
}

.Slider > div:nth-child(1) {
  margin: 0 3em;
}

.Slider > button:before{
  font-family: "Font Awesome 5 Free"; font-weight: 900; content: "\f104";
  font-size: 40px;
  background: #ffffff;
  padding: 10px 20px;
  -webkit-transition: font-size 0.3s;
  -o-transition: font-size 0.3s;
  transition: font-size 0.3s;
  border-radius: 1em;
  -webkit-box-shadow: 0 0 10px #80808085;
          box-shadow: 0 0 10px #80808085;
}

.Slider > button:nth-child(1) {
  left: 0;
  z-index: 10;
  -webkit-transform: translate(0, -100%);
      -ms-transform: translate(0, -100%);
          transform: translate(0, -100%);
}

.Slider > button:nth-child(3) {
  right: 0;
  -webkit-transform: translate(-200%, -100%);
      -ms-transform: translate(-200%, -100%);
          transform: translate(-200%, -100%);
}

.Slider > button:hover:before, .Slider > button:nth-child(3):hover:before {
  font-size: 45px;
  -webkit-box-shadow: 0 0 10px #808080e8;
          box-shadow: 0 0 10px #808080e8;
}

.Slider > button:nth-child(3):before {
  font-family: "Font Awesome 5 Free"; font-weight: 900; content: "\f105";
  font-size: 40px;
  background: #ffffff;
  padding: 10px 20px;
  -webkit-transition: font-size 0.3s;
  -o-transition: font-size 0.3s;
  transition: font-size 0.3s;
  border-radius: 1em;
  -webkit-box-shadow: 0 0 10px #80808085;
          box-shadow: 0 0 10px #80808085;
}

.Slider button:before {
  color: black;
}

.Facility {
  text-align: center;
}

.Icon i {
  font-size: 3em;
}

.Name {
  margin: 1em 0;
  font-size: 1.4em;
}



